.MainNav {
    width: 100vw;
}
.nav{
    width:100vw;
}
.head{
    font-size: 20px;
    text-decoration: none;
    color: #000;
}
.topic{
    display: flex;
    flex-direction: row;
    column-gap: 0.2cm;
    align-items: center;
    text-decoration: none;
}

.cover2 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.5cm;
}

.menu {
    display: none;
}

.icon {
    width: 1.5cm;
    height: 1.5cm;
}

.logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 1cm;
}

.SideNav {
    min-width: 4cm;
    border-top: thin solid grey; 
}

.cover1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.item {
    font-size: 16px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #000000;
    text-decoration: none;
    font-size: 16px;
    padding-bottom: 5px;
}

.item:hover {
    /* text-decoration: underline; */
    color: #3f3f3f9d;    
}

.active {
    color: rgb(118, 0, 151);
    font-weight: bold;
}

.border-thin {
    border: thin solid grey;
    width: 100%;
    border-left: none;
    border-top: none;
    margin:2px 0px;
    padding:3px 0px;
}

.overlay {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    padding: 1cm 0cm 0.5cm 0.2cm;
    /* row-gap: 0.5cm; */
    height: 100%;
    /* width: 4cm; */
    position: fixed;
    z-index: 1;
    background-color: rgb(255, 255, 255);
    top: 0;
    left: 0;
    overflow: hidden;
    transition: 1s ease-in-out;
    display: none;
    border: thin solid grey;
   
}

.overlayItems {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    row-gap: 0.1cm;
    cursor: pointer;
    color: rgb(85, 85, 85);
}

.cls-btn {
    width: 0.8cm;
    height: 0.8cm;
    cursor: pointer;
    border-radius: 50%;
}

@media only screen and (max-width:750px) {
    .SideNav {
        display: none;
    }
    .menu {
        display: flex;
    }
    .box{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }


}

@media only screen and (min-width:1440px) {
    .icon {
        height: 1.5cm;
        width: 1.5cm;
    }
}

@media only screen and (min-width:740px) and (max-width:1440px) {
    .icon {
        height: 1.3cm;
        width: 1.3cm;
    }
    .head{
        font-size: 16px;
    }
    
}

@media only screen and (max-width:740px) {
    .icon {
        height: 1cm;
        width: 1cm;
    }
    .logo{
        column-gap: 0.5cm;
    }
    
    
}

@media only screen and (min-width:500px) and (max-width:740px){
    .head{
        font-size: 13px;
    }


}

@media only screen and (max-width:500px){
    .head{
        display: none;
    }
    .nav{
        width:100vw;
       
    }
    .cover2{
        padding: 0.2cm;
    }

}